import styles from './slot.module.css'
import dobby from '../../images/dobby.jpg'
import dumbledore from '../../images/dumbledore.jpg'
import harry from '../../images/harry.jpg'
import hermione from '../../images/hermione.jpg'
import hogwarts from '../../images/hogwarts.jpg'
import metla from '../../images/metla.jpg'
import nagini from '../../images/nagini.jpg'
import ron from '../../images/ron.jpg'
import snape from '../../images/snape.jpg'
import voldemort from '../../images/voldemort.jpg'
import React from 'react'
//import ModalOpen from '../Modals'

class Slot extends React.Component {

    state = {
        symbols: [dobby, dumbledore, harry, hermione, hogwarts, metla, nagini, ron, snape, voldemort],
        demoAmount: 5000,
        spinAmount: 100,
        winAmount: 0
    }
    handleEntailmentRequest = () => {
        // let img_1 = document.getElementById('first')
        // let img_2 = document.getElementById('second')
        // if (img_1.src===img_2.src) {
        //     this.setState({
        //         winAmount: 100,
        //     })
        // }
        // if (!!this.state.winAmount) {
        //     this.setState({
        //         spinAmount: 'get' + this.state.winAmount
        //     })
        // }
        this.setState({
            symbols: this.state.symbols,
            demoAmount: this.state.demoAmount - 100 + this.state.winAmount,
        })
    }
   
    render() {
        return (
            <>
                <div>
                    <h1>Harry Slot</h1>
                </div>

                <div className={styles.mainSlot}>
                    <div className={styles.slotRow}>
                        <div className={styles.slotCol}>
                            <img id='first' src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                        <div className={styles.slotCol}>
                            <img id='second' src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                        <div className={styles.slotCol}>
                            <img id='third' src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                    </div>

                    <div className={styles.slotRow}>
                        <div className={styles.slotCol}>
                            <img src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                        <div className={styles.slotCol}>
                            <img src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                        <div className={styles.slotCol}>
                            <img src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                    </div>
                    <div className={styles.slotRow}>
                        <div className={styles.slotCol}>
                            <img src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                        <div className={styles.slotCol}>
                            <img src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                        <div className={styles.slotCol}>
                            <img src={this.state.symbols[Math.floor(Math.random() * 10)]} alt='sim' />
                        </div>
                    </div>
                </div>
                <div className={styles.numbers}>
                    <div className={styles.amount}>
                        {this.state.demoAmount} AMD
                    </div>
                    <div>
                        <button
                            className={styles.spin}
                            onClick={this.handleEntailmentRequest}
                        >
                            {this.state.spinAmount}
                        </button>
                    </div>
                    <div className={styles.amount}>
                        {this.state.winAmount} AMD
                    </div>
                </div>
                {/* <button>
                    modal
                    <ModalOpen />
                </button> */}


            </>
        )
    }
}

export default Slot